.tariffs {
  padding-top: 0;

  &__title {
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include high-tablet {
      gap: 20px;
    }

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__item {
    grid-column: 4 span;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 20px;
    border: 1px solid rgba(20, 16, 36, 0.20);
    padding: 40px;

    @include high-tablet {
      padding: 20px;
    }

    @include mobile {
      grid-column: 4 span;
      padding: 40px 20px;
      width: 300px;
      margin: auto;
    }
  }

  &__subtitle {
    display: flex;
    align-items: center;
    color: #141024;
    font-size: 30px;
    font-weight: 600;
    line-height: 160%; /* 48px */
    margin-bottom: 60px;

    @include high-tablet {
      margin-bottom: 20px;
    }

    @include mobile {
      margin-bottom: 40px;
    }

    &::before {
      content: '';
      width: 20px;
      height: 3px;
      margin-right: 10px;
      background: var(--dark-color);
    }
    &::after {
      content: '';
      width: 20px;
      height: 3px;
      margin-right: 0px;
      margin-left: 10px;
      background: var(--dark-color);
    }
  }

  &__price {
    margin-bottom: 20px;
    color: var(--dark-color);
    font-size: 50px;
    font-weight: 600;
    line-height: 160%; /* 80px */

    @include high-desktop {
      font-size: 45px;
    }

    @include high-tablet {
      font-size: 30px;
    }

    @include mobile {
      font-size: 50px;
    }
  }

  &__descr {
    text-align: center;
    color: var(--dark-color);
    font-size: 18px;
    font-weight: 400;
    line-height: 160%; /* 28.8px */
    margin-bottom: auto;

    @include high-tablet {
      font-size: 16px;
    }
  }

  &__btn {
    margin-top: 61px;
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
  }


  &__item--current {
    background: var(--dark-color);
    transform: scaleY(1.1);
    .tariffs__subtitle {
      color: var(--light-color)
    }
    .tariffs__price {
      color: var(--light-color)
    }
    .tariffs__descr {
      color: var(--light-color)
    }

    @include high-tablet {
      transform: scaleY(1);
    }
  }
}

