.school {
  background: #FFF;
  &__descr {
    max-width: 891px;
    color: var(--dark-color);
    font-size: 30px;
    line-height: 160%;
    margin-bottom: 60px;

    @include mobile {
      font-size: 20px;
      margin-bottom: 40px;
    }
  }

  &__descr--bold {
    color: var(--dark-color);
    font-size: 30px;
    font-weight: 600;
    line-height: 160%; /* 48px */
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__item {
    grid-column: 3 span;

    @include high-desktop {
      grid-column: 6 span;
    }
    @include mobile {
      grid-column: 4 span;
      text-align: center;
    }
  }

  &__item-number {
    color: var(--accent-color);
    font-family: Jost;
    font-size: 70px;
    font-weight: 600;
    line-height: 160%; /* 112px */
  }

  &__item-text {
    color: rgba(20, 16, 36, 0.70);
    font-size: 18px;
    line-height: 160%; /* 28.8px */
  }
}

