.hero {
  position: relative;
  min-height: 800px;
  background-color: var(--dark-color);
  padding-top: 160px;

  &__container {
    @include flex-all-sb;
    align-items: center;

    @include small-tablet {
      flex-direction: column;
    }
  }

  &__content {
    max-width: 516px;

    @include small-tablet {
      max-width: 75%;
      text-align: center;
    }

    @include mobile {
      max-width: 100%;
    }
  }

  &__title {

    margin: 0;
    font-size: 70px;
    font-weight: 600;
    line-height: 160%;
    color: var(--light-color);
    margin-bottom: 60px;

    @include high-tablet {
      font-size: 50px;
    }
    @include small-tablet {
      font-size: 30px;
      margin-bottom: 290px;
    }
  }
  &__subtitle {
    display: block;
    font-size: 20px;
    line-height: 160%;

    @include small-tablet {
      font-size: 16px;
    }
  }
  &__image {
    transform: translateX(167px);

    @include high-tablet {
      transform: translateX(0px);
    }

    @include small-tablet {
      width: 273px;
      height: 273px;
      position: absolute;
      bottom: 120px;
      z-index: 5;
    }
  }

  &__btn {
    @include small-tablet {
      width: 100%;
      position: relative;
      z-index: 10;
      margin-bottom: 55px;
      padding: 20px 31px !important;
      font-size: 25px !important;
    }
  }

  @include high-tablet {
    min-height: auto;
  }

  @include small-tablet {
    align-items: center;
    padding-top: 100px;
  }

}
