.get {
  background: #F9F9F9;
  padding: 100px 0;

  @include mobile {
    padding: 40px 0;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include mobile {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__item {
    grid-column: 4 span;

    @include high-desktop {
      grid-column: 6 span;
    }

    @include mobile {
      grid-column: 4 span;
      text-align: center;
    }
  }

  &__item-title {
    color: var(--dark-color);
    font-size: 24px;
    font-weight: 600;
    line-height: 160%; /* 38.4px */
    margin-bottom: 20px;
  }

  &__item-descr {
    color: var(--text-color);
    font-size: 18px;
    line-height: 160%; /* 28.8px */
  }

  &__item--sign {
    position: relative;
    display: inline-block;
    &:hover .get__item--brief{
        display: block;
      }
    &::after{
      position: absolute;
      right: -34px;
      top: 0;
      content: "";
      display: block;
      width: 24px;
      height: 24px;
      background-image: url('../img/get.svg');

    }
  }

  &__item--brief {
    display: none;
    width: max-content;
    position: absolute;
    left: 110%;
    top: -167%;
    color: #141024;
    font-size: 16px;
    line-height: 160%; /* 25.6px */
    padding: 15px 20px;
    border-radius: 5px;
    background: #FFF;
    box-shadow: 0px 2px 8px 0px rgba(20, 16, 36, 0.10);

    @include mobile {
      left: -87%;
    }
  }
}

