.footer {
  background: var(--dark-color);
  padding: 40px 0;

  &__container {
    display: flex;
    align-items: start;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
      align-items: center;
    }
  }

  &__left {
    @include mobile {
      text-align: center;
      margin-bottom: 40px;
    }
  }

  &__copy {
    color: rgba(255, 255, 255, 0.70);
    font-size: 16px;
    line-height: 160%; /* 25.6px */
    margin-top: 5px;
  }

  .phone {
    @include mobile {
      font-size: 20px;
    }
  }
}

