.header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  padding: 30px 0;
  z-index: 1000;

  &__container {
    @include flex-all-sb;

    @include high-tablet {
      justify-content: initial;
    }
  }

  &__logo {
    @include small-tablet {
      width: 136px;
      height: 13.95px;
    }
  }

  &__phone {
    @include high-tablet {
      position: relative;
      width: 44px;
      height: 44px;
      border: 1px solid rgba(255, 255, 255, 0.08);
      border-radius: 10px;
      background: url('../img/call.svg') center center no-repeat;
      font-size: 0;
      margin-left: auto;
      margin-right: 10px;
    }
  }

}
