.burger {
  display: none;
  position: relative;
  width: 44px;
  height: 44px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 10px;

  @include high-tablet {
    display: block;
  }

  &__line {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 26px;
    height: 2px;
    background-color: var(--light-color);
    border-radius: 100px;

    &:first-child {
      margin-top: -7px;
    }

    &:last-child {
      margin-top: 7px;
      width: 13px;
      left: auto;
      right: 50%;
      transform: translate(0%, -50%);

    }
  }
}


.burger__close {
  display: none;
  position: absolute;
  right: 0;
  top: 10px;
  width: 44px;
  height: 44px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  background: url("../img/close.svg") center center no-repeat;
  background-size: 14px 12.6px;
  margin-left: auto;
  margin-right: 10px;

  @include high-tablet {
    display: block;
  }
}
