.trainers {
  background-color: #fff;
  &__wrapper {
    padding-left: max(15px,  calc((100% - var(--content-width)) / 2));
  }
  &__content {
    margin-bottom: 77px;

    @include mobile {
      margin-bottom: 41px;
    }
  }
  &__container {
    padding-left: 0;
    margin-left: 0;
  }
  &__title {
    color: var(--dark-color);
    font-size: 50px;
    font-weight: 600;
    line-height: 160%; /* 80px */
    margin-bottom: 60px;

    @include mobile {
      font-size: 30px;
      margin-bottom: 40px;
    }
  }
  &__name {
    color: var(--dark-color);
    font-size: 24px;
    font-weight: 600;
    line-height: 160%; /* 38.4px */
    margin-top: 20px;
    margin-bottom: 10px;

    @include mobile {
      font-size: 20px;
    }
  }

  &__post {
    color: var(--text-color);
    font-size: 18px;
    line-height: 160%; /* 28.8px */
    margin-bottom: 20px;

    @include high-desktop {
      font-size: 19px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__btn {
    color: var(--accent-color);
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 160%; /* 28.8px */

    @include mobile {
      font-size: 16px;
    }
  }

  &__nav {
    @include flex-all-sb;
  }

  &__scroll {
    border-radius: 40px;
    background: rgba(20, 16, 36, 0.08);
    width: 360px;
    height: 5px;
  }

  &__slider-btn {
    @include bg-cover;

    width: 40px;
    height: 40px;
  }

  &__slider-btn--prev {
    background-image: url("../img/prev.svg")
  }
  &__slider-btn--next {
    background-image: url("../img/next.svg")
  }

  &__btns {
    @include mobile {
      display: none;
    }
  }
}
.swiper-scrollbar-drag {
  background: #141024;
}
