.graph-modal__close {
  color: rgba(20, 16, 36, 0.70);
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%; /* 28.8px */
  width: auto;
  height: auto;
  top: 40px;
  right: 40px;
  background-image: none;

  @include mobile {
    top: 15px;
  }
}

.graph-modal {
  &::before {
    @include tablet {
      display: none;
    }
  }
}

.popup {
  width: 100%;
  max-width: 940px;
  padding: 40px;

  @include mobile {
    padding: 40px 20px !important;
  }

  &__bottom-content {
    max-height: 375px;
    padding-right: 20px;
  }

  &__top {
    display: flex;
    margin-bottom: 40px;
  }

  &__image {
    margin-right: 40px;

    @include mobile {
      margin-right: 20px;
    }
  }

  &__name {
    color: var(--dark-text);
    font-size: 24px;
    font-weight: 600;
    line-height: 160%; /* 38.4px */
    margin-bottom: 10px;
  }

  &__post {
    color: var(--text-color);
    font-size: 18px;
    line-height: 160%; /* 28.8px */
    margin-bottom: 10px;
  }

  &__text {
    color: var(--dark-color);
    font-size: 20px;
    font-style: normal;
    line-height: 160%; /* 32px */
  }

  &__subtittle {
    color: var(--dark-color);
    font-size: 24px;
    font-weight: 600;
    line-height: 160%; /* 38.4px */
    margin: 40px 0 20px 0;
  }

  .simplebar-track.simplebar-vertical {
    border-radius: 40px;
    background: rgba(20, 16, 36, 0.08);
    width: 5px;
  }
  .simplebar-scrollbar::before {
    left: 0;
    right: 0;
    border-radius: 40px;
    background: var(--dark-color);
    width: 5px;
    opacity: 1;
  }

  @include small-tablet {
    padding-top: 80px;
  }
}

.social {
  display: flex;
  align-items: center;

  &__item {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  &__link {
    display: block;
    width: 30px;
    height: 30px;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: var(--dark-color);
      object-fit: cover;
    }
  }
}

.tabs {

  &__menu {
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(20, 16, 36, 0.11);
    margin-bottom: 20px;
  }

  &__nav {
    max-width: 750px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include mobile {
      flex-wrap: wrap;
    }
  }

  &__nav-item {
    @include mobile {
      margin-right: 20px;
    }
  }

  &__nav-btn {
    color: var(--dark-color);
    font-size: 20px;
    line-height: 160%; /* 32px */

    &:focus {
      background-color: transparent;
      color: var(--accent-color);
    }

    &--active {
      background-color: transparent;
      outline: none;
      color: var(--accent-color);
    }
  }

}



