.form {

  &__label {
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: 10px;
    width: 100%;
  }

  &__input {
    width: 100%;
    height: 64px;
    padding: 0 20px;
    border-radius: 5px;
    background: #F6F6F6;
    font-size: 18px;
    font-weight: 400;
    line-height: normal;
    &::placeholder{
      color: var(--text-color);
    }
  }

  &__btn.btn--dark {
    border-radius: 5px;
    background: var(--dark-color);
    padding: 19px;
  }

  .just-validate-error-label {
    margin-bottom: 10px;
    color: var(--light-color) !important;
  }
}
