.nav {
  &__list {
    @include flex-v-center;

    @include high-tablet {
      @include flex-all-center;
      flex-direction: column;
      width: 100%;
      height: 100%;
    }
  }
  &__item {
    &:not(:last-child){
      margin-right: 40px;
    }
    @include high-tablet {
      margin-right: 0 !important;
      margin-bottom: 30px;
    }
  }
  &__link {
    color: var(--light-color);
    font-family: Jost;
    font-size: 1.25rem;
    line-height: 160%; /* 2rem */
    @include high-tablet {
      font-size: 30px;
    }
  }

  @include high-tablet {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    background-color: var(--dark-color);
    z-index: 500;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}
.menu--active {
  opacity: 1;
  visibility: visible;
}
