.order {
  @include image-set('../img/percent');

  padding: 87px 0;
  background-color: var(--accent-color);
  background-position: bottom center;
  background-size: cover;

  @include mobile {
    padding: 40px 0;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);

    @include mobile{
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__left {
    grid-column: 5 span;
    padding-left: 60px;

    @include mobile {
      grid-column: 4 span;
      padding-left: 0;
    }
  }

  &__title {
    color: var(--light-color);
    font-size: 36px;
    font-weight: 600;
    line-height: 130%; /* 46.8px */
    margin-bottom: 20px;

    @include high-tablet {
      font-size: 26px;
    }

    @include mobile {
      text-align: center;
    }
  }

  &__descr {
    color: var(--light-color);
    font-size: 20px;
    line-height: 130%; /* 26px */

    @include mobile {
      margin-bottom: 40px;
      text-align: center;
    }
  }

  &__right {
    grid-column: 7 / 5 span;
    // padding-right: 60px;

    @include mobile {
      grid-column: 4 span;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
  }
}



